/* eslint-disable no-undef */
import React, { useEffect, useRef, useState } from 'react'
import $ from "jquery";
import FormModal from './FormModal';
import { delBox, delUser, delDelivery } from '../services/delRequest';


//tr td type
function Table(props) {
    $.DataTable = require('datatables.net');
    $.buttons = require('datatables.net-buttons');

    const formTrUser = ['Email','Username', 'Password', 'Role'];
    const formTrDelivery = ['Target Box', 'Target Customer ID', 'Assigned Deliverer ID'];
    const formTrBox = ['Box Name', 'Street Name', 'RFID'];

    const formTrUserEdit = ['Email','Username', 'Role', 'Token'];
    const formTrDeliveryEdit = ['Target Box', 'Target Customer ID', 'Assigned Deliverer ID'];
    const formTrBoxEdit = ['RFID', 'Street_Name', 'Box Name', ];
    const [formType, setFormType] = useState('');
    const [formTr, setFormTr] = useState([]);
    const [editID, setEditID] = useState('');

    const [isShown, setIsShown] = useState(false);  
    const [newData, setNewData] = useState([]);    

 
    //reference to the dataTable
    const tableRef = useRef();

    

    useEffect(() => {
        console.log(tableRef.current);
        //can use all the APIs on table now
 
        const table = $(tableRef.current).DataTable(
            {
                data: props.td,
                columnDefs: [
                    {
                        'targets': 0,
                        'searchable':false,
                        'orderable':false,
                        'render': function (data, type, full, meta){
                            return '<input type="checkbox" name="id[]" value="'
                               + $('<div/>').text(data).html() + '">';
                        }
                    },
                    {
                        'targets': 7,
                        'searchable':false,
                        'orderable':false,
                        'render': function () {
                            return `<button  class='btn btn-info btn-sm'>Edit</button>`;
                        },
                    }
                ],
                select: {
                    style: 'os',
                    selected: 'td:first-child'
                },
                order: [[1, 'asc']],
                columns: [
                    { title: ""},
                    { title: props.tr[0],},
                    { title: props.tr[1],},
                    { title: props.tr[2],},
                    { title: props.tr[3],},
                    { title: props.tr[4],},
                    { title: props.tr[5],
                        'render': function (qrCode){
                            return '<img src=' + qrCode + ' width="40px">';
                        }
                    },
                    { title: ""},
                ],
                scrollX: true,
                dom: 'Bfrtip',
                buttons: true,
                searching: true,
                destroy: true,
                paging: true,
            }
        );

       

        table.button().add( 0, {
            text: "Delete",
            action: function ( e, dt, button, config){
                e.preventDefault();
                var $checkboxes = $('#dataTable td input[type="checkbox"]');
                var countChecked = $checkboxes.filter(':checked').length;

                console.log(countChecked);
                if(countChecked === 0) {
                    e.preventDefault();
                    window.alert("Please select at least one row");
                    return false;
                };

                var checkedIndexes = [];
                var checkedIds = [];
                          
                $checkboxes.filter(':checked').each(function() {
                    checkedIds.push(table.row($(this).parent().parent()).data()[1]);
                    checkedIndexes.push($checkboxes.index($(this)));
                    console.log($(this).parent().parent());
                    table.row( $(this).parent().parent()).remove().draw();
                });
                console.log(checkedIndexes);
                console.log(checkedIds);
                //get userID instead of indexes
                switch (props.type) {
                    case 'User':
                        delUser(checkedIds);
                        break;
                    case 'Box':
                        delBox(checkedIds);
                        break;
                    case 'Delivery':
                        delDelivery(checkedIds);
                        break;
                    default:
                        break;
                } 
            
            }
        });

        table.button().add( 1, {
            text: "Add",
            action: function ( e, dt, button, config){
                e.preventDefault();
                switch (props.type) {
                    case 'User':
                        setFormTr(formTrUser);
                        break;
                    case 'Box':
                        setFormTr(formTrBox);
                        break;
                    case 'Delivery':
                        setFormTr(formTrDelivery);
                        break;
                    default:
                        break;
                }
                setFormType("add");
                setIsShown(true);
                //after the form is submitted, callback to get input data 
                // tab.row.add(newData);
                // //table.row.add(['', '000001', '234@gmail.com', 'Deliverer', '354qwcqw', 'None', '2022/12/29 23:59:00']).draw();
                // table.$('td').addClass('editable');
            }
        });
     
        //click on the edit button
        $('tbody tr').on('click', 'button', function() {
            //get the row data
            console.log( table.row($(this).parents("tr")).data());
            
            setEditID(table.row($(this).parents("tr")).data()[1]);
            //show update popup
            //need to enable disable
            switch (props.type) {
                case 'User':
                    setFormTr(formTrUserEdit);
                    break;
                case 'Box':
                    setFormTr(formTrBoxEdit);
                    break;
                case 'Delivery':
                    setFormTr(formTrDeliveryEdit);
                    break;
                default:
                    break;
            }
            setFormType("update");
            setIsShown(true);
        });
          
        return function() {
            console.log("Table Destroyed");
            table.destroy();
        }

    },[formTr, formTrBox, formTrDelivery, formTrUser, props, props.td, props.tr]);



    return (
        <div>
            <table id='dataTable' className='display' width='100%' ref={ tableRef }></table>
            {isShown ? <FormModal isShown={isShown} type={props.type} formContent={formTr} 
            setNewData={()=>setNewData()} formType={formType} editID={editID}/>
        : null}
        </div>
    )
}

export default Table